
.frame {
  position: relative;
  float: left;
  width: 63mm;
  height: 88mm;
  margin: 0px 0.5px;
}

.card {
  position: absolute;
  width: 63mm;
  height: 88mm;
  z-index: 10;
}

.input {
  position: absolute;
  left: 5px;
  bottom: 7px;
  z-index: 20;
  width: 55mm;
}

@media print{
    .input{
        display: none;
    }
}

.printFrame {
  width: 717.4px;
}

